@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  margin: 0px auto;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: linear-gradient(hsl(273, 75%, 66%), hsl(240, 73%, 65%));
}

.card_card__36kzh {
  max-width: 950px; 
  min-height: 500px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  background: white;
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  border-radius: 25px;
  font-family: 'Kumbh Sans', sans-serif;
}

.card_logoContainer__22Bqa {
  position: relative;
  overflow: hidden;
}

h1 {
  color: hsl(238, 29%, 16%);
}

.card_desktopBg__1rHoE {
  position: absolute;
  width: 190%;
  -webkit-transform: translate(-56%, -31%);
          transform: translate(-56%, -31%);
}

.card_desktopWoman__2tEAk {
  position: absolute;
  width: 100%;
  -webkit-transform: translate(-17%, 20%);
          transform: translate(-17%, 20%);
}

.card_box__1rYv9 {
  position: absolute;
  -webkit-transform: translate(-56%, 110%);
          transform: translate(-56%, 110%);
  -webkit-animation: card_slidein__3PDfg 2s linear 0s infinite alternate;
          animation: card_slidein__3PDfg 2s linear 0s infinite alternate;
}

@-webkit-keyframes card_slidein__3PDfg {
  from {
    -webkit-transform: translate(-56%, 110%);
            transform: translate(-56%, 110%);
  }
  to {
    -webkit-transform: translate(-46%, 110%);
            transform: translate(-46%, 110%);
  }
}

@keyframes card_slidein__3PDfg {
  from {
    -webkit-transform: translate(-56%, 110%);
            transform: translate(-56%, 110%);
  }
  to {
    -webkit-transform: translate(-46%, 110%);
            transform: translate(-46%, 110%);
  }
}

.card_accordionContainer__3he3o {
  margin-top: 3rem;
}

@media (max-height: 450px) {
  .card_card__36kzh {
    min-height: -webkit-max-content;
    min-height: max-content;
  }
}

@media (max-width: 1000px) {
  .card_card__36kzh {
    margin: 0 30px;
  }
}

@media (max-width: 450px) {
  .card_card__36kzh {
    grid-template-columns: 1fr;
    grid-template-rows: 110px 1fr;
  }

  .card_logoContainer__22Bqa {
    -webkit-transform: translate(0);
            transform: translate(0);
    overflow: visible;
  }

  .card_desktopBg__1rHoE {
    -webkit-transform: translate(25%, -55%);
            transform: translate(25%, -55%);
    width: 70%;
  }

  .card_desktopWoman__2tEAk {
    -webkit-transform: translate(25%, -60%);
            transform: translate(25%, -60%);
    width: 70%;
  }

  .card_box__1rYv9 {
    display: none;
  }

  .card_accordionContainer__3he3o {
    margin: 0 auto;
    text-align: center;
    padding: 0 20px 10px 20px;
  }
}
.accordion__section {
  width: 80%;
  border-bottom: hsl(240, 5%, 91%) 1px solid;
}

/* button container */
.accordion {
  margin-top: 5px;
  width: 100%;
  padding-left: 0;
  padding-right: 20px;
  font-family: 'Kumbh Sans', sans-serif;
  font-size: 15px;
  color: hsl(237, 12%, 33%);
  background-color: #ffffff;
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  border: none;
  outline: none;
}

.accordion.active {
  color: hsl(238, 29%, 16%);
  font-weight: 700;
}

.accordion:hover {
  color: hsl(14, 88%, 65%);
}

.arrowIcon {
  transition: -webkit-transform 0.6s ease;
  transition: transform 0.6s ease;
  transition: transform 0.6s ease, -webkit-transform 0.6s ease;
}

.rotate {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.accordion__content {
  overflow: hidden;
  transition: max-height 0.6s ease;
}

.accordion__text {
  padding-right: 30px;
  padding-bottom: 15px;
  font-size: 12px;
  color: hsl(240, 6%, 50%);
}

@media (max-width: 450px) {
  .accordion__section {
    width: 100%;
  }

  .accordion {
    padding-right: 0;
  }
}
